.mapContainer {
    display:flex;
    background-color: var(--primary-color);
    margin: 5%;
    flex-flow: row wrap;
    align-content: flex-start;
  }
  
  .mapTitle {
    flex-basis: 100%;
    text-align: center;
    padding: 5px;
  }
  
  .mapDivider {
    width: 50%;
    margin: 0 auto;
    margin-bottom: 50px;
  }
  
  .bookstoreNames {
    padding-left: 50px;
    padding-bottom: 5px;
    flex-basis: 100%;
  }

  .hoursText {
    padding-left: 50px;
    padding-bottom: 100px;
    padding-right: 50px;
    flex-grow: 1;
  }

  .googleMap {
    padding-left: 50px;
    padding-bottom: 100px;
    padding-right: 50px;
    flex-grow: 2;
  }

  .bookstoreImages {
    padding-left: 50px;
    padding-bottom: 100px;
    padding-right: 50px;
    width: 400px;
    height: 400px;
  }

 .closestBookstoreButtonContainer {
    padding-left: 50px;
    padding-right: 50px;
    flex-basis: 100%;
    text-align: center;
    overflow: auto;
 }

 .closestBookstoreButton {
    width: 350px;
    height: 50px;
    font-size: 110%;
 }

 .closestBookstoreText {
    padding-left: 50px;
    padding-right: 50px;
    flex-basis: 100%;
    text-align: center;
    font-size: 110%;
    overflow: auto;
 }